@import url(https://fonts.googleapis.com/css?family=Lato:200, 400);
@import url(https://fonts.googleapis.com/css?family=Lustria);
.Header {
	height: 40px;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: normal;
	        align-items: normal;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.Header h1 {
	margin: 0;
}

.Header a {
	color: #444;
	text-decoration: none;
}

.Header__tagline--wide {
	opacity: 0.35;
	margin-left: 10px;
}

.Header__tagline--narrow {
	opacity: 0.35;
	display: block;
	text-align: center;
	display: none;
	margin-top: 10px;
}

.Header__not-logged-in {
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
}

.Header__not-logged-in a:last-child {
	background: rgb(255, 121, 121);
	color: white;
	padding: 5px 15px;
	border-radius: 15px;
	margin-left: 10px;
}

.Header__logged-in a {
	border: 1px solid rgba(0, 0, 0, 0.25);
	padding: 5px 15px;
	border-radius: 15px;
}

@media screen and (min-width: 450px) {
	.Header {
		-webkit-align-items: baseline;
		        align-items: baseline;
	}

	.Header h1 {
		font-size: 22pt;
	}
}

@media screen and (max-width: 490px) {
	.Header__tagline--wide {
		display: none;
	}

	.Header__tagline--narrow {
		display: inherit;
	}
}


.Hyph {
  margin-right: 5px;
  margin-left: 5px;
}

.Input,
.Textarea {
  border: 1px solid #ddd;
  display: block;
  font-size: 1.1em;
  margin-bottom: 10px;
  padding: 10px;
  text-align: left;
  width: 100%;
  border-radius: 4px;
}

.Input::-webkit-input-placeholder,
.Textarea::-webkit-input-placeholder {
  color: #ccc;
}

.Input::-ms-input-placeholder,
.Textarea::-ms-input-placeholder {
  color: #ccc;
}

.Input::placeholder,
.Textarea::placeholder {
  color: #ccc;
}

.Textarea {
  resize: vertical;
}

.Button {
  -webkit-order: 1px solid #aaa;
          order: 1px solid #aaa;
  background-color: #eee;
  font-size: 1.1em;
  border-radius: 4px;
  padding: 8px 12px;
  font-weight: bold;
}

.Required {
  color: #CA1551;
  font-family: Arial;
  font-weight: bold;
}

.Section {
  margin-left: 20px;
  margin-right: 20px;
}
.Section--list {
  margin-left: 10px;
  margin-right: 10px;
}

.App {
	padding-top: 20px;
	padding-bottom: 20px;
	max-width: 960px;
	margin: 0 auto;
}

.App__header {
	padding-left: 20px;
