.FoodListItem {
	background: none;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	border: 0;
	font-size: smaller;
	text-decoration: none;
	box-shadow: 0px 11px 21px -8px rgba(0, 0, 0, 0.5);
	margin-bottom: 40px;
	margin-right: 20px;
	border-radius: 10px;
}

/* .FoodListItem:hover {
  cursor: pointer;
} */

.FoodListItem,
.FoodListItem:visited {
	color: #555;
}

.FoodListItem__image {
	height: 250px;
	width: 275px;
	border-radius: 10px 10px 0 0;
	background-size: cover;
	background-position: 50% 50%;
}

.FoodListItem__details {
	padding: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 275px;
}

.FoodListItem__details h2 {
	margin: 0;
}

.FoodListItem__details {
	padding: 5px 0;
	/* text-align: center; */
	opacity: 0.85;
}

.FoodListItem__details span {
	display: block;
	text-align: center;
	opacity: 0.5;
}

ul.FoodListItem__composites {
	list-style: none;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.FoodListItem__composites li {
	padding: 10px;
}

.FoodListPage {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}

.SearchPage label {
	display: none;
}

.SearchPage input {
	border-radius: 10px;
	background: none;
	border: 0;
	box-shadow: 0px 11px 21px -8px rgba(0, 0, 0, 0.5);
	padding: 10px;
	margin-bottom: 40px;
	margin-right: 20px;
	text-decoration: none;
